"use client"
import React, { createContext, useContext, useState } from "react";

type ShopingCartContextProviderPros = {
    children: React.ReactNode
}

type CartItems = {
    id: number,
    qty: number,
};


type TShopingCartContext = {
    cartItems: CartItems[];
    handleIncreaseProductQty: (id: number) => void;
    getProductQty: (id: number) => number
};

const ShopingCartContext = createContext({} as TShopingCartContext);


export const useShopingCartContext = () => {
    return useContext(ShopingCartContext);
}


export function ShopingCartContextProvider({ children, }: ShopingCartContextProviderPros) {

    const [cartItems, setCartItems] = useState<CartItems[]>([]);



    const total



    const getProductQty = (id: number) => {
        return cartItems.find((item) => item.id == id)?.qty || 0
    }
 



    const handleIncreaseProductQty = (id: number) => {
        setCartItems((currentItem) => {
            let isNotProductExist = currentItem.find(item => item.id == id) == null

            if (isNotProductExist) {
                return [...currentItem, { id: id, qty: 1 }];
            }

            else {
                return currentItem.map((item) => {
                    if (item.id == id) {
                        return {
                            ...item,
                            qty: item.qty + 1,
                        };
                    } else {
                        return item;
                    }

                });
            }
        });
    }

    return (
        <ShopingCartContext.Provider value={{ cartItems, handleIncreaseProductQty, getProductQty }}>
            {children}
        </ShopingCartContext.Provider>
    );
}